.page {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.main {
  grid-template-columns: repeat(4, 1fr);
  gap: 46px 48px;
  width: fit-content;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 1023px) {
  .main {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width <= 767px) {
  .main {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width <= 487px) {
  .main {
    grid-template-columns: 1fr;
  }
}

.header {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.img-logo {
  align-items: center;
  display: flex;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  display: flex;
}

.nav__item:nth-child(n+2) {
  margin-left: 20px;
}

.nav__link {
  color: #060b35;
  padding: 23px 0;
  text-decoration: none;
}

.nav__link:hover {
  color: #00acdc;
  transition: all .3s;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card__stars {
  display: flex;
}

.card__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__stars--1 .card__star:nth-child(-n+1), .card__stars--2 .card__star:nth-child(-n+2), .card__stars--3 .card__star:nth-child(-n+3), .card__stars--4 .card__star:nth-child(-n+4), .card__stars--5 .card__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  max-width: 160px;
  min-height: 134px;
  margin: 0 auto;
}

.card__name {
  color: #060b35;
  margin-top: 40px;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin-top: 17px;
  font-size: 10px;
  font-weight: 400;
  display: flex;
}

.card__reviews-count {
  margin: 0 0 1px;
}

.card-price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card-price__text {
  color: #616070;
  margin: 0;
  font-weight: 400;
  line-height: 18px;
}

.card-price__rate {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  margin-top: 16px;
  padding-bottom: 3px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
  border-radius: 5px;
  transition: all .3s;
}

.card:hover {
  transition: all .3s;
  transform: scale(1.2);
}

.card:hover .card__name {
  color: #34568b;
}
